import React, { Component, PropTypes } from 'react';
import { Motion, spring } from 'react-motion';
import './ProjectPage.css';

export default class ProjectPage extends Component {
  static propTypes = {};
  constructor(props) {
    super(props);
    this.state = {
      atProject: 1,
      motion: {
        project1: {
          rotateZ: 0,
          // x: 0,
          // y: 0,
          opacity: 1
        },
        project2: {
          rotateZ: 45,
          // x: 200,
          // y: 100,
          opacity: 0
        }
      }
    }
  }

  switchProject = (next) => {
    const {atProject} = this.state;
    if (next) {
      (atProject < 2) && this.setState({
        atProject: atProject + 1,
        motion: {
          project2: {
            rotateZ: spring(0),
            // x: spring(0),
            // y: spring(0),
            opacity: spring(1)
          },
          project1: {
            rotateZ: spring(-45),
            // x: spring(-200),
            // y: spring(100),
            opacity: spring(0)
          }
        }
      });
    } else {
      (atProject > 0) && this.setState({
        atProject: atProject - 1,
        motion: {
          project1: {
            rotateZ: spring(0),
            // x: spring(0),
            // y: spring(0),
            opacity: spring(1)
          },
          project2: {
            rotateZ: spring(45),
            // x: spring(200),
            // y: spring(100),
            opacity: spring(0)
          }
        }
      })
    }
  };

  render() {
    const { motion: {project1, project2 } } = this.state;
    return (
      <div className="page projectPage">
        <h1 className="page__title">PROJECTS</h1>

        <div className="projects">
          <Motion style={project1}>
            {({ rotateZ, x, y, opacity }) =>
              <div className="project project1" style={{
                opacity,
                transform: `rotateZ(${rotateZ}deg)`
              }}>
                <div className="projectThumbnail">
                  <img src={require('../../assets/frodev_thumbnail.png')} alt=""/>
                </div>
                <div className="projectCaption">
                  <p className="name lead">Frodev</p>
                  <p className="description">Bookmark Manager</p>
                </div>
              </div>
            }
          </Motion>
          <Motion style={project2}>
            {({ rotateZ, x, y, opacity }) =>
              <div className="project project2" style={{
                opacity,
                transform: `rotateZ(${rotateZ}deg)`
              }}>
                <div className="projectThumbnail">
                  <img src={require('../../assets/frodev_thumbnail.png')} alt=""/>
                </div>
                <div className="projectCaption">
                  <p className="name lead">Tilt Hover</p>
                  <p className="description">A React Component implementing hover effect inspired by Codrop</p>
                </div>
              </div>
            }
          </Motion>
        </div>

        <div className="sliderControl">
          <a onClick={() => this.switchProject()}>Prev</a>
          <a onClick={() => this.switchProject(true)}>Next</a>
        </div>
      </div>
    );
  }
}



